.hds-icon {
  --icon-size: 100%;

  background-color: currentcolor;
  display: inline-block;
  height: var(--icon-size);
  -webkit-mask-position: center;
  mask-position: center;
  -webkit-mask-repeat: no-repeat;
  mask-repeat: no-repeat;
  -webkit-mask-size: auto;
  mask-size: auto;
  width: var(--icon-size);
}

.hds-icon.hds-icon--size-xs {
  --icon-size: var(--spacing-layout-2-xs);
}

.hds-icon.hds-icon--size-s {
  --icon-size: var(--spacing-layout-xs);
}

.hds-icon.hds-icon--size-m {
  --icon-size: var(--spacing-layout-s);
}

.hds-icon.hds-icon--size-l {
  --icon-size: var(--spacing-layout-m);
}

.hds-icon.hds-icon--size-xl {
  --icon-size: var(--spacing-layout-l);
}

.hds-icon--cross {
  -webkit-mask-image: url("data:image/svg+xml;charset=utf-8,%3Csvg role='img' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E %3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M18 7.5L13.5 12L18 16.5L16.5 18L12 13.5L7.5 18L6 16.5L10.5 12L6 7.5L7.5 6L12 10.5L16.5 6L18 7.5Z' fill='currentColor'%3E%3C/path%3E %3C/svg%3E");
  mask-image: url("data:image/svg+xml;charset=utf-8,%3Csvg role='img' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E %3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M18 7.5L13.5 12L18 16.5L16.5 18L12 13.5L7.5 18L6 16.5L10.5 12L6 7.5L7.5 6L12 10.5L16.5 6L18 7.5Z' fill='currentColor'%3E%3C/path%3E %3C/svg%3E");
}

.hds-tag {
  --tag-background: var(--color-black-10);
  --tag-color: var(--color-black-90);
  --tag-font-size: var(--fontsize-body-s);
  --tag-spacing: var(--spacing-2-xs);
  --tag-height: 32px;

  align-items: center;
  background-color: var(--tag-background);
  border-radius:    20px;
  color: var(--tag-color);
  display: inline-flex;
  flex-direction:   row;
  font-size: var(--tag-font-size);
  line-height: var(--tag-height);
  min-height: var(--tag-height);
  outline: none;
}

.hds-tag--large {
  --tag-font-size:  var(--fontsize-body-m);
  --tag-spacing:    var(--spacing-s);
  --tag-height:     48px;

  border-radius:    24px;
  flex-direction:   row;
}

.hds-tag:focus,
.hds-tag:focus-within {
  box-shadow: 0 0 0 3px var(--color-focus-outline);
}

.hds-tag[tabindex='0'] {
  cursor: pointer;
}

.hds-tag__label {
  line-height: var(--lineheight-m);
  padding: 0 var(--tag-spacing);
}

.hds-tag__label:not(:only-child) {
  padding: 0 0 0 var(--tag-spacing);
}

.hds-tag__delete-button {
  display: flex;
  margin-right: calc(var(--tag-spacing) / 2);
  outline: none;
 }

.hds-tag__delete-button .hds-icon {
  height: var(--spacing-m);
  width: var(--spacing-m);
}
